refresh(qtype) {
var _this = this;
if (qtype == undefined) {
_this.currpage = 0;
}
_this.$vux.loading.show({text: _this.$t('loading')})
var qurl = _this.requestUrl + "sciencepopularization/sciencepopularizations"
