console.log(_this.propagandaArr)

} else {
var data = datalist;
for (var i = 0; i < data.length; i++) {
_this.propagandaArr.push(data[i])
}
}
} else {
if (qtype == undefined) {
_this.propagandaArr = [];
var dataObj={"detailUrl":"fbc","publishtime":"2019-03-05 14:52:21","title":_this.$t("StormSurge")};
_this.propagandaArr.splice(0,0,dataObj);
}

}

}

